import { useState } from 'react';
import { useTheme } from '@mui/material/styles';
import { useDispatch, useSelector } from 'react-redux';

import {
    IconButton,
    Stack,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    Grid,
    Divider,
    Typography,
    TableHead,
    TableRow,
    Tooltip,
    Button,
    CircularProgress
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import ChangeStatusDialog from './changeStatus';

import Avatar from 'ui-component/extended/Avatar';
import moment from 'moment';

const DeliveryDashboard = ({ deliveryList, user }) => {
    console.log('deliveryList', deliveryList);
    const theme = useTheme();
    const [open, setOpen] = useState(false);
    const [deliveryId, setDeliveryId] = useState(0);
    return (
        <>
            <ChangeStatusDialog setOpen={setOpen} open={open} user={user} setDeliveryId={setDeliveryId} deliveryId={deliveryId} />
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell align="center" sx={{ borderBottom: 'none' }}></TableCell>
                            <TableCell align="left " className="Tableheading" sx={{ borderBottom: 'none' }}>
                                Product name{' '}
                            </TableCell>
                            {/*   <TableCell   className='Tableheading' sx={{borderBottom:'none'}}>Location</TableCell> */}
                            <TableCell className="Tableheading" sx={{ borderBottom: 'none' }}>
                                Description
                            </TableCell>

                            <TableCell className="Tableheading" sx={{ borderBottom: 'none' }}>
                                Price
                            </TableCell>
                            <TableCell className="Tableheading" sx={{ borderBottom: 'none' }}>
                                Status
                            </TableCell>
                            <TableCell className="Tableheading" sx={{ borderBottom: 'none' }}>
                                Address
                            </TableCell>

                            <TableCell className="Tableheading" sx={{ borderBottom: 'none' }}>
                                Actions
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <>
                        {(deliveryList == undefined && deliveryList?.length == 0) || undefined ? (
                            <>
                                <Grid item>
                                    <Typography style={{ padding: '20px', fontWeight: '800', justifyContent: 'center' }}>
                                        {' '}
                                        No Data Available
                                    </Typography>
                                </Grid>
                            </>
                        ) : (
                            <TableBody>
                                {deliveryList != undefined &&
                                    deliveryList?.map((row, index) => (
                                        <>
                                            <TableRow>
                                                <TableCell align="right" sx={{ borderBottom: 'none' }}></TableCell>

                                                <TableCell
                                                    sx={{
                                                        display: 'flex',
                                                        borderBottom: 'none',
                                                        textTransform: 'capitalize',
                                                        borderBottom: 'none'
                                                    }}
                                                >
                                                    <Grid item lg={6}>
                                                        <Avatar alt="Brand Image" src={row?.Nft.asset} sx={{}} />
                                                    </Grid>
                                                    <Grid item lg={6} className="tableName">
                                                        {row?.Nft.name}
                                                    </Grid>
                                                </TableCell>

                                                <TableCell className="tablecell" sx={{ borderBottom: 'none' }}>
                                                    {row?.Nft.description} 
                                                </TableCell>
                                                <TableCell
                                                    className="tablecell"
                                                    sx={{
                                                        borderBottom: 'none',
                                                        color:
                                                            row?.status == 'Delivered'
                                                                ? 'Green'
                                                                : row?.status == 'pending'
                                                                ? 'Blue'
                                                                : 'Orange'
                                                    }}
                                                >
                                                    {row?.Nft.price} {row?.Nft.currencyType}
                                                </TableCell>
                                                <TableCell
                                                    className="tablecell"
                                                    sx={{
                                                        borderBottom: 'none',
                                                        color:
                                                            row?.status == 'Delivered'
                                                                ? 'Green'
                                                                : row?.status == 'pending'
                                                                ? 'Blue'
                                                                : 'Orange'
                                                    }}
                                                >
                                                    {row?.status}
                                                </TableCell>
                                                <TableCell className="tablecell" sx={{ borderBottom: 'none' }}>
                                                {row?.User?.address}
                                                </TableCell>
                                                <TableCell align="left" sx={{ borderBottom: 'none' }}>
                                                    <Stack direction="row" justifyContent="left">
                                                        <Tooltip placement="top" title="Status">
                                                            <Button
                                                                variant="outlined"
                                                                sx={{ fontFamily: 'Poppins', fontStyle: 'normal' }}
                                                                endIcon={<SendIcon />}
                                                                onClick={() => {
                                                                    setOpen(true);
                                                                    setDeliveryId(row?.id);
                                                                }}
                                                            >
                                                                Status
                                                            </Button>
                                                        </Tooltip>
                                                    </Stack>
                                                </TableCell>
                                            </TableRow>
                                        </>
                                    ))}
                            </TableBody>
                        )}
                    </>
                </Table>
                <Divider />
            </TableContainer>
        </>
    );
};

export default DeliveryDashboard;
